import { createUserWithEmailAndPassword, updateProfile } from "firebase/auth";
import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import { auth } from "../../firebase";
import { SignupWrapper, ErrorMessageWrapper, SignupBtn } from "./styled";
import { ErrorMessage, LogoInfo } from "../Login/styled";
import Img from "../../components/common/Img/Img";
import LogoIcon from "../../assets/images/logo.png";
import Input from "../../components/common/Input/Input";

export const Signup = () => {
  const [nickname, setNickname] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [errorMsg, setErrorMsg] = useState("");
  const navigate = useNavigate();

  const onChangeNickname = (e) => {
    setNickname(e.target.value);
  };

  const onChangeEmail = (e) => {
    setEmail(e.target.value);
  };

  const onChangePassword = (e) => {
    setPassword(e.target.value);
  };

  //    
  const onSubmitLoginInfo = async (e) => {
    e.preventDefault();
    try {
      // Firebase   
      const createdUser = await createUserWithEmailAndPassword(
        auth,
        email,
        password
      );

      //   UID  nickname Firestore 
      await updateProfile(createdUser.user, {
        displayName: nickname,
      });

      //     
      navigate("/MyTodoSite");
    } catch (error) {
      switch (error.code) {
        case "auth/invalid-email":
          setErrorMsg("  .");
          break;
        case "auth/email-already-in-use":
          setErrorMsg("  .");
          break;
        case "auth/weak-password":
          setErrorMsg(" 6  .");
          break;
        default:
      }
    }
  };

  return (
    <SignupWrapper>
      <form onSubmit={onSubmitLoginInfo}>
        <Img src={LogoIcon} width="178px" height="65px" />
        <LogoInfo>    </LogoInfo>
        <Input
          type="email"
          placeholder="  "
          value={email}
          onChange={(e) => onChangeEmail(e)}
        />
        <Input
          type="text"
          placeholder="  "
          onChange={(e) => onChangeNickname(e)}
          value={nickname}
        />
        <Input
          type="password"
          placeholder="   (6 )"
          onChange={(e) => onChangePassword(e)}
          value={password}
        />
        <ErrorMessageWrapper>
          {errorMsg && <ErrorMessage>{errorMsg}</ErrorMessage>}
        </ErrorMessageWrapper>
        <SignupBtn type="submit" disabled={!email || !nickname || !password}>
          
        </SignupBtn>
      </form>
    </SignupWrapper>
  );
};
