import React, { useRef, useState } from "react";
import {
  DeleteButton,
  DeleteModal,
  InfoEditButtonWrapper,
  ModalBox,
  ModalHeader,
  ModalWrapper,
  ProfileImgWrapper,
} from "./styled";
import Img from "../common/Img/Img";
import BasicProfileIcon from "../../assets/images/basicProfile.png";
import Input from "../common/Input/Input";
import { deleteUser, updateProfile } from "firebase/auth";
import { useNavigate } from "react-router-dom";
import { ref, getDownloadURL, uploadBytes } from "firebase/storage";
import { storage } from "../../firebase";

export const UserInfoModal = ({ onClickModal, userObj }) => {
  const [newNickname, setNewNickname] = useState(userObj?.displayName);
  const inputRef = useRef(null);
  const [hidden, setHidden] = useState(true);
  const [modalhidden, setModalHidden] = useState(true);
  const navigate = useNavigate();
  const [newPhotoURL, setNewPhotoURL] = useState(userObj.photoURL);

  //   
  const onClickImgEdit = () => {
    console.log("");
    inputRef.current.click();
  };

  //    
  const onChangeImage = async (e) => {
    let image = inputRef.current.files[0];
    const storageRef = ref(storage, `images/${image.name}`);
    uploadBytes(storageRef, image).then((snapshot) => {
      getDownloadURL(storageRef).then(async (url) => {
        setNewPhotoURL(url);
        await updateProfile(userObj, {
          photoURL: url,
        });
      });
    });
  };

  //  
  const onClickNicknameEdit = () => {
    setHidden(!hidden);
  };

  //   
  const onChangeNickname = async (e) => {
    if (
      newNickname &&
      e.key === "Enter" &&
      e.nativeEvent.isComposing === false
    ) {
      await updateProfile(userObj, {
        displayName: newNickname,
      });
      setHidden(!hidden);
    }
  };

  //  
  const onClickUserInfoDelete = () => {
    setModalHidden(!modalhidden);
  };

  // 
  const onClickDelete = async () => {
    await deleteUser(userObj);
    alert(" !");
    navigate("/MyTodoSite");
  };

  return (
    <ModalWrapper>
      <ModalBox>
        <ModalHeader>
           <button onClick={onClickModal}>X</button>
        </ModalHeader>
        <div>
          <ProfileImgWrapper>
            <Img
              width="50px"
              height="50px"
              src={
                userObj.photoURL === null ? `${BasicProfileIcon}` : newPhotoURL
              }
            />

            {hidden ? (
              <span>{userObj && `${userObj.displayName}`} </span>
            ) : (
              <Input
                value={newNickname}
                onChange={(e) => setNewNickname(e.target.value)}
                onKeyDown={(e) => onChangeNickname(e)}
              />
            )}
          </ProfileImgWrapper>
          <InfoEditButtonWrapper>
            <button onClick={onClickImgEdit}>
              <input
                type="file"
                ref={inputRef}
                onChange={(e) => onChangeImage(e)}
              />
                
            </button>
            |<button onClick={onClickNicknameEdit}> </button>
          </InfoEditButtonWrapper>
        </div>
        <DeleteButton onClick={onClickUserInfoDelete}>
           
        </DeleteButton>
      </ModalBox>
      {modalhidden ? null : (
        <DeleteModal>
           ?
          <ul>
            <li onClick={onClickDelete}></li>
          </ul>
        </DeleteModal>
      )}
    </ModalWrapper>
  );
};
